<template>
  <div>
    <!-- Komponen lain dalam template -->
    <p v-if="token">Token: {{ token }}</p>
    <p v-else>Tidak ada token yang ditemukan dalam cookie.</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      token: null,
    };
  },
  mounted() {
    // Panggil metode untuk mengambil token dari cookie
    this.getTokenFromCookie();
  },
  methods: {
    getTokenFromCookie() {
      // Dapatkan semua cookie
      const cookies = document.cookie.split(";");

      // Cari cookie dengan nama "token"
      for (const cookie of cookies) {
        const [name, value] = cookie.split("=");

        if (name.trim() === "token") {
          // Dapatkan token dan atur dalam data komponen
          this.token = value.trim();
          break; // Keluar dari loop jika token ditemukan
        }
      }
    },
  },
};
</script>

<style>
/* CSS styling jika diperlukan */
</style>
